body {
  margin: 0;
  padding: 0;
  background-color: #7e7f7e;
  color: white;
  font-size: 32px;
  font-family: "GBP OTF 1.05";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}



.nav-link {
	color: white;
}

.nav-link:hover {
	color: white;
	text-decoration: underline;
}

.nav-item {
	color: white;
}

.nav-item:hover {
	color: white;
	text-decoration: none;
}

.svgContainer {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
}

.sliderContainer {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
}

.slider {
	width: 80%;
	margin: auto;
}

